import styled from "styled-components";
import { NavLink } from "react-router-dom";
import Profile from "../assets/logo.jpg";
import React from "react";
import AccountCircleOutlinedIcon from "@material-ui/icons/AccountCircleOutlined";
import {connect} from 'react-redux'
import {useHistory} from 'react-router-dom'
import {logoutUser} from "../auth/actions/userActions";



function Header({authenticated,logoutUser}) {
  console.log(authenticated)
  const history = useHistory();
  return (
    <Nav>
      <NavLink exact to="/">
        <Logo>
          <img src={Profile} alt="Las Antillas" />
        </Logo>
      </NavLink>

      <NavMenu>
        <NavLink exact to="/">
          
          <span>HOME</span>
        </NavLink>
        <NavLink exact to="/favorite" className="favorite">
          <a>
            <span>FAVORITES</span>
          </a>
        </NavLink>
      

     
      </NavMenu>

{!authenticated? 
      <SignOut>
        <AccountCircleOutlinedIcon style={{ fontSize: 50,color:"white" }} />
        <DropDown>
          <NavLink exact to="/login">
            <span>Log in</span>
          </NavLink>
          <br />
          <NavLink exact to="/signup">
            <span>Sing Up</span>
          </NavLink>
        </DropDown>
      </SignOut> : 
    <SignOut>
    <AccountCircleOutlinedIcon style={{ fontSize: 50,color:"white" }} />
    <DropDown>
      <NavLink exact to="/" onClick={()=>logoutUser(history)}>
        <span>Logout</span>
      </NavLink>
     
    </DropDown>
  </SignOut>  
      }
    </Nav>
  );
}

const Nav = styled.nav`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  background-color: #090b13;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 36px;
  letter-spacing: 16px;
  z-index: 3;
`;

const Logo = styled.a`
  padding: 0;
  width: 80px;
  margin-top: 4px;
  max-height: 70px;
  font-size: 10px;
  display: inline-block;
  img {
    display: block;
    width: 100%;
    max-height: 70px;
  }
`;
const UserImg = styled.img`
  height: 100%;
`;

const NavMenu = styled.div`
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  height: 100%;
  justify-content: flex-end;
  margin: 0px;
  padding: 0px;
  position: relative;
  margin-right: auto;
  margin-left: 25px;
  @media (max-width: 468px) {
    //display: none;
  }
  a {
    display: flex;
    align-items: center;
    padding: 0 12px;
    @media (max-width: 768px) {
      padding: 0 6px;
    }
    img {
      height: 20px;
      min-width: 20px;
      width: 20px;
      z-index: auto;
    }
    span {
      color: rgb(249, 249, 249);
      font-size: 13px;
      letter-spacing: 1.42px;
      line-height: 1.08;
      padding: 2px 0px;
      white-space: nowrap;
      position: relative;
      &:before {
        background-color: rgb(249, 249, 249);
        border-radius: 0px 0px 4px 4px;
        bottom: -6px;
        content: "";
        height: 2px;
        left: 0px;
        opacity: 0;
        position: absolute;
        right: 0px;
        transform-origin: left center;
        transform: scaleX(0);
        transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
        visibility: hidden;
        width: auto;
      }
    }
    &:hover {
      span:before {
        transform: scaleX(1);
        visibility: visible;
        opacity: 1 !important;
      }
    }
  }
  .favorite {
    @media (max-width: 768px) {
      justify-content: space-between;
    }
  }
`;

const DropDown = styled.div`
  position: absolute;
  top: 48px;
  right: 0px;
  background: rgb(19, 19, 19);
  border: 1px solid rgba(151, 151, 151, 0.34);
  border-radius: 4px;
  box-shadow: rgb(0 0 0 / 50%) 0px 0px 18px 0px;
  padding: 10px;
  font-size: 14px;
  letter-spacing: 3px;
  width: 100px;
  opacity: 0;
  color: rgb(249, 249, 249);
  a {
    text-decoration: none;
    color: rgb(249, 249, 249);
  }
`;

const SignOut = styled.a`
  position: relative;
  height: 48px;
  width: 48px;
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  &:hover {
    ${DropDown} {
      opacity: 1;
      transition-duration: 1s;
    }
  }
`;

const mapStateToProps = ({session}) => ({
  authenticated:session.authenticated
})
export default connect(mapStateToProps,{logoutUser})(Header) ;